.section-04 {
    height: 2087px;
    // background: #000 url('../images/section-04-bg.jpg') no-repeat center bottom;
    // background-size: auto 100%;
    position: relative;
    z-index: 1;
    &__ticket {
        width: 587px;
        height: 279px;
        margin: 0 auto;
        transform: translate3d(1%,-3%,0);
        img {
            @extend %rsp-img;
        }
    }
    &__text {
        font-size: 32px;
        color: #fff;
        text-align: center;
        line-height: 1.6;
        transform: translate3d(0,-28%,0);
    }
    &__btn-more {
        width: 324px;
        height: 156px;
        display: block;
        margin: 0 auto;
        transform: translate3d(0,-2%,0);
        position: relative;
        img {
            @extend %rsp-img;
            position: relative;
            z-index: 0;
        }
        &::after {
            content: "";
            width: 100%;
            height: 100%;
            background: transparent url('../images/section-04-btn-more-on@2x.png') no-repeat center top;
            background-size: cover;
            position: absolute;
            left: 0;
            top: 0;
            opacity: 0;
            transition: all 300ms ease;
            z-index: 1;
        }
        &:hover {
            &::after {
                opacity: 1;
            }
        }
    }
}
