html {
    font-family: 'Microsoft JhengHei';
    width: 100%;
    -webkit-font-smoothing: antialiased;
}

body {
    width: 100%;
    background-color: #000;
}

.container {
    @include wrap;
}

.wrap {
    max-width: 1920px;
    margin: 0 auto;
    overflow: hidden;
    background: #000 url('../images/all-bg.jpg') no-repeat center top;
    background-size: auto 100%;
    height: 7806px;
}

.inner {
    width: 768px;
    height: 100%;
    margin: 0 auto;
    position: relative;
}

.btn-gotop {
    width: 154px;
    height: 154px;
    background: transparent url('../images/btn-gotop-all@2x.png') no-repeat center top;
    background-size: 154px auto;
    left: 50%;
    position: fixed;
    bottom: 0;
    transform: translate3d(250%,-50%,0);
    border: 0;
    opacity: 0;
    transition: all 300ms ease;
    pointer-events: none;
    z-index: 300;
    &.active {
        opacity: 1;
        pointer-events: auto;
    }
}