.section-kv {
    $self: &;
    height: 1236px;
    background-color: #82258c;
    position: relative;
    z-index: 0;
    overflow: hidden;
    // &::after {
    //     width: 100%;
    //     height: 1237px;
    //     position: absolute;
    //     left: 0;
    //     background: transparent url('../images/tttttt.jpg') no-repeat center top;
    //     background-size: auto 1237px;
    //     content: "";
    //     z-index: 999;
    //     opacity: 0.5;
    //     pointer-events: none;
    // }
    &__frame {
        width: 100%;
        height: 1236px;
        background: transparent url('../images/kv-bg.png') no-repeat center bottom;
        background-size: auto 100%;
        position: absolute;
        z-index: 10;
    }
    &__video {
        overflow: hidden;
        position: absolute;
        width: 100%;
        height: calc(100% - 1px);
        z-index: 0;
        video {
            z-index: 1;
            position: absolute;
            width: auto;
            height: 100%;
            left: 50%;
            top: 0;
            transform: translate3d(-50%,0,0);
        }
        &::before {
            width: 100%;
            height: 1237px;
            background: transparent url('../images/kv-video-texture.png') no-repeat center top;
            background-size: auto 1237px;
            position: absolute;
            z-index: 10;
            content: "";
            left: 0;
            top: 0;
        }
        &::after {
            width: 2828px;
            height: 197px;
            background: transparent url('../images/kv-light.png') no-repeat center top;
            background-size: auto 197px;
            position: absolute;
            z-index: 12;
            content: "";
            left: 50%;
            transform: translate3d(-55%,14%,0);;
            mix-blend-mode: lighten;
        }
    }
    &__nav {
        z-index: 100;
        top: 0;
        left: 50%;
        transform: translate3d(-50%,0,0);
        position: absolute;
        display: flex;
        width: 100%;
        height: 106px;
        justify-content: center;
        gap: 48px;
        align-items: center;
    }
    &__btn-link {
        font-size: 32px;
        color: #fff;
        display: block;
        text-align: center;
        line-height: 1.2;
        transition: all 300ms ease;
        &:hover {
            text-shadow: 0 0 10px #ff9000, 0 0 6px #ff9000, 0 0 20px #ff9000;
        }
        &.active {
            pointer-events: none;
            text-shadow: 0 0 10px #ff9000, 0 0 6px #ff9000, 0 0 20px #ff9000;
        }
    }
    &__cht {
        width: 1237px;
        height: 1279px;
        position: absolute;
        z-index: 5;
        left: 50%;
        top: 50%;
        transform: translate3d(-27.8%,-34%,0);
        img {
            @extend %rsp-img;
        }
    }
    &__monster {
        width: 320px;
        height: 288px;
        position: absolute;
        z-index: 15;
        left: 50%;
        top: 50%;
        transform: translate3d(-133.8%,-133%,0);
        img {
            @extend %rsp-img;
        }
    }
    &__sub-title-1 {
        width: 438px;
        height: 73px;
        position: absolute;
        z-index: 15;
        left: 50%;
        top: 50%;
        transform: translate3d(-49.8%,-84.7%,0);
        img {
            @extend %rsp-img;
        }
    }
    &__sub-title-2 {
        width: 411px;
        height: 64px;
        position: absolute;
        z-index: 15;
        left: 50%;
        top: 50%;
        transform: translate3d(-49.8%,48.3%,0);
        img {
            @extend %rsp-img;
        }
    }
    &__main-title {
        width: 797px;
        height: 328px;
        position: absolute;
        z-index: 20;
        left: 50%;
        top: 50%;
        transform: translate3d(-49.5%,14.8%,0);
        img {
            @extend %rsp-img;
            position: relative;
            z-index: 0;
        }
    }
    &__light {
        position: absolute;
        left: 50%;
        top: 50%;
        z-index: 10;
        &--yellow {
            background: transparent url('../images/kv-light-yellow@3x.png') no-repeat center top;
        }
        &--blue {
            background: transparent url('../images/kv-light-blue@3x.png') no-repeat center top;
        }
        &--1 {
            width: 72px;
            height: 72px;
            transform: translate3d(-520%,-234%,0);
            background-size: cover;
            animation: opacity 0.5s 2s infinite alternate ease-in-out;
        }
        &--2 {
            width: 54px;
            height: 54px;
            transform: translate3d(-715%,14%,0);
            background-size: cover;
            animation: opacity 0.7s 2s infinite alternate ease-in-out;
        }
        &--3 {
            width: 44px;
            height: 44px;
            transform: translate3d(-483%,-261%,0);
            background-size: cover;
            animation: opacity 1s 2s infinite alternate ease-in-out;
        }
        &--4 {
            width: 40px;
            height: 40px;
            transform: translate3d(-472%,-73%,0);
            background-size: cover;
            animation: opacity 0.9s 2s infinite alternate ease-in-out;
        }
        &--5 {
            width: 36px;
            height: 36px;
            transform: translate3d(388%,-189%,0);
            background-size: cover;
            animation: opacity 0.5s 2s infinite alternate ease-in-out;
        }
        &--6 {
            width: 48px;
            height: 48px;
            transform: translate3d(266%,54%,0);
            background-size: cover;
            animation: opacity 0.8s 2s infinite alternate ease-in-out;
        }
        &--7 {
            width: 70px;
            height: 70px;
            transform: translate3d(396%,-245%,0);
            background-size: cover;
            animation: opacity 0.6s 2s infinite alternate ease-in-out;
        }
        &--8 {
            width: 60px;
            height: 60px;
            transform: translate3d(507%,-9%,0);
            background-size: cover;
            animation: opacity 0.8s 2s infinite alternate ease-in-out;
        }
    }
    &__btn-play {
        cursor: pointer;
        display: block;
        width: 137px;
        height: 121px;
        position: absolute;
        left: 50%;
        top: 50%;
        z-index: 100;
        transform: translate3d(-50%,241%,0);
        &::before {
            content: "";
            position: absolute;
            left: 0;
            top: 0;
            width: 100%;
            height: 100%;
            background: transparent url('../images/kv-btn-play-arrow-off@3x.png') no-repeat center top;
            background-size: cover;
            transition: all 300ms ease;
        }
        &::after {
            content: "";
            position: absolute;
            left: 0;
            top: 0;
            width: 100%;
            height: 100%;
            background: transparent url('../images/kv-btn-play-arrow-on@3x.png') no-repeat center top;
            background-size: cover;
            opacity: 0;
            transition: all 300ms ease;
        }
        &:hover {
            &::after {
                opacity: 1;
            }
            #{$self}__circle-1,
            #{$self}__circle-2 {
                &::before {
                    opacity: 0;
                }
                &::after {
                    opacity: 1;
                }
            }
        }
    }
    &__circle-1 {
        width: 167px;
        height: 167px;
        position: absolute;
        left: -8%;
        top: -14%;
        z-index: 15;
        animation: rotate 3s infinite linear forwards;
        transform-origin: 80px 80px;
        &::before {
            content: "";
            position: absolute;
            left: 0;
            top: 0;
            width: 100%;
            height: 100%;
            background: transparent url('../images/kv-btn-play-line-blue-off@3x.png') no-repeat center center;
            background-size: cover;
            transition: all 300ms ease;
        }
        &::after {
            content: "";
            position: absolute;
            left: 0;
            top: 0;
            width: 100%;
            height: 100%;
            background: transparent url('../images/kv-btn-play-line-blue-on@3x.png') no-repeat center center;
            background-size: cover;
            opacity: 0;
            transition: all 300ms ease;
        }
    }
    &__circle-2 {
        width: 162px;
        height: 162px;
        position: absolute;
        left: -13%;
        top: -17%;
        z-index: 10;
        animation: rotate 3s infinite linear reverse forwards;
        transform-origin: 80px 80px;
        &::before {
            content: "";
            position: absolute;
            left: 0;
            top: 0;
            width: 100%;
            height: 100%;
            background: transparent url('../images/kv-btn-play-line-pink-off@3x.png') no-repeat center center;
            background-size: cover;
            transition: all 300ms ease;
        }
        &::after {
            content: "";
            position: absolute;
            left: 0;
            top: 0;
            width: 100%;
            height: 100%;
            background: transparent url('../images/kv-btn-play-line-pink-on@3x.png') no-repeat center center;
            background-size: cover;
            opacity: 0;
            transition: all 300ms ease;
        }
        &:hover {
            &::before {
                opacity: 0;
            }
            &::after {
                opacity: 1;
            }
        }
    }
    &__circle-3 {
        width: 137px;
        height: 137px;
        position: absolute;
        left: 2%;
        top: 1%;
        z-index: -1;
        &::before {
            content: "";
            position: absolute;
            left: 0;
            top: 0;
            width: 100%;
            height: 100%;
            background: transparent url('../images/home-btn-play-bg@3x.png') no-repeat center center;
            background-size: cover;
            transition: all 300ms ease;
        }
        &:hover {
            &::before {
                opacity: 0;
            }
            &::after {
                opacity: 1;
            }
        }
    }
}

@keyframes opacity {
	0% {opacity:0}
	100% {opacity:1}
}

@keyframes rotate {
	0% {transform: rotate(0deg)}
	100% {transform: rotate(360deg)}
}