.section-03 {
    height: 1020px;
    // background: #000 url('../images/section-03-bg.jpg') no-repeat center bottom;
    // background-size: auto 100%;
    position: relative;
    z-index: 1;
    &__ticket {
        width: 587px;
        height: 279px;
        margin: 0 auto;
        transform: translate3d(1%,-4%,0);
        img {
            @extend %rsp-img;
        }
    }
    &__text {
        font-size: 32px;
        color: #fff;
        text-align: center;
        line-height: 1.6;
        transform: translate3d(0,-24%,0);
    }
    &__slider {
        transform: translate3d(1px,18px,0);
    }
}


.section-03-slider {
    width: 710px;
    padding-bottom: 100px;
    img {
        @extend %rsp-img;
    }
    .swiper-button-next {
        width: 40px;
        height: 72px;
        background: transparent url('../images/btn-next-off@3x.png') no-repeat 0 0;
        background-size: cover;
        top: 196px;
        &::after {
            display: none;
        }
        &:hover {
            background-image: url('../images/btn-next-on@3x.png');
        }
    }
    .swiper-button-prev {
        width: 40px;
        height: 72px;
        background: transparent url('../images/btn-prev-off@3x.png') no-repeat 0 0;
        background-size: cover;
        top: 196px;
        &::after {
            display: none;
        }
        &:hover {
            background-image: url('../images/btn-prev-on@3x.png');
        }
    }
    .swiper-pagination-bullet {
        outline: none;
        width: 56px;
        height: 56px;
        background: transparent url('../images/pagination-off@2x.png') no-repeat 0 0;
        background-size: cover;
        opacity: 1;
        margin-left: 16px !important;
        margin-right: 16px !important;
        &.swiper-pagination-bullet-active {
            background-image: url('../images/pagination-on-03@2x.png');
            outline: none;
        }
    }
    .swiper-pagination {
        bottom: 9px;
    }
}