.section-05 {
    height: 1306px;
    // background: #000 url('../images/section-05-bg.jpg') no-repeat center top;
    // background-size: auto 1306px;
    position: relative;
    z-index: 1;
    &__ticket {
        width: 587px;
        height: 279px;
        margin: 0 auto;
        transform: translate3d(1%,-3%,0);
        img {
            @extend %rsp-img;
        }
    }
    &__date {
        font-size: 32px;
        color: #fff;
        text-align: center;
        line-height: 1.6;
        transform: translate3d(0,-3%,0);
    }
    &__text {
        font-size: 32px;
        color: #fff;
        text-align: center;
        line-height: 1.6;
        transform: translate3d(0,0,0);
    }
    &__btn-more {
        width: 324px;
        height: 156px;
        display: block;
        margin: 0 auto;
        transform: translate3d(0,5%,0);
        position: relative;
        img {
            @extend %rsp-img;
            position: relative;
            z-index: 0;
        }
        &::after {
            content: "";
            width: 100%;
            height: 100%;
            background: transparent url('../images/section-05-btn-more-on@2x.png') no-repeat center top;
            background-size: cover;
            position: absolute;
            left: 0;
            top: 0;
            opacity: 0;
            transition: all 300ms ease;
            z-index: 1;
        }
        &:hover {
            &::after {
                opacity: 1;
            }
        }
    }
    &__total {
        width: 297px;
        height: 248px;
        margin: 0 auto;
        transform: translate3d(0,28px,0);
        img {
            @extend %rsp-img;
        }
    }
    &__arrow {
        width: 92px;
        height: 64px;
        margin: 0 auto 35px;
        transform: translate3d(0,28px,0);
        padding-top: 14px;
        img {
            @extend %rsp-img;
        }
    }
    &__box {
        transform: translate3d(0,17px,0);
        margin: 0 auto;
        box-sizing: border-box;
        background-color: rgba(0,0,0,0.67);
        border: solid 1px #c90cff;
        width: 656px;
        height: 236px;
        display: flex;
        justify-content: center;
        align-items: center;
        gap: 28px;
    }
    &__box-img {
        width: 172px;
        height: 172px;
        img {
            @extend %rsp-img;
        }
    }
    &__ul-list {
        li {
            font-size: 32px;
            color: #fff;
            padding-left: 32px;
            position: relative;
            &:not(:last-child) {
                margin-bottom: 6px;
            }
            &::before {
                content: "";
                position: absolute;
                width: 18px;
                height: 18px;
                background: transparent url('../images/section-05-icon@2x.png') no-repeat 0 0;
                background-size: cover;
                left: -4px;
                top: 9px;
            }
        }
    }
}
