.modal {
    position: fixed;
    z-index: 1;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0,0,0,0.9);
    pointer-events: none;
    display: flex;
    justify-content: center;
    align-items: center;
    opacity: 0;
    z-index: 200;
    transition: all 500ms ease;
    &.active {
        opacity: 1;
        pointer-events: auto;
    }
    &__content {
        position: relative;
        width: 768px;
    }
    &__video-frame {
        background: transparent url('../images/popup-ui-playui@2x.png') no-repeat 0 0;
        background-size: cover;
        width: 764px;
        height: 447px;
        position: relative;
        iframe,
        video {
            position: absolute;
            top: 50%;
            left: 50%;
            width: 719px;
            height: 405px;
            transform: translate3d(-50%,-52%,0);
        }
    }
    &__btn-close {
        width: 127px;
        height: 106px;
        position: absolute;
        top: 0;
        right: 0;
        -webkit-appearance: none;
        cursor: pointer;
        text-decoration: none;
        text-align: center;
        padding: 0;
        color: #fff;
        border: 0;
        background: transparent url('../images/popup-ui-btn-close@2x.png') no-repeat 0 0;
        background-size: cover;
        transform: translate3d(0,-110px,0);
    }
}